.page {
  min-width: @min-width;
  height: 100%;
  margin: 0 auto;

  font-family: var(--font-page);
  font-size: 16px;
  line-height: 117.2%;
  font-weight: 400;
  // .font-basic();
  -webkit-text-size-adjust: 100%;
}

.page__body {
  display: flex;
  flex-direction: column;

  min-height: 100%;
  margin: 0;

  color: var(--color-font-basic);
  background-color: var(--color-back-basic);
}

.page__main {
  flex-grow: 1;
}
